import './App.css'
import Cli from './components/pages/Cli'
import Home from './components/pages/Home'
import Menu from './components/pages/Menu'
import Topo from './components/pages/Topo'


export default function App() {
  return (
	  <>
	<Topo/>
	  <Router basename={process.env.PUBLIC_URL}  className='container '>
	
		<div className='cicle center'>
			<div className='menu'>
				<Menu/>
			</div>
			<div className='conteudo'>
				<Switch>
					<Route path="/" exact component={Home}/>
					<Route path="/clientes" exact component={Cli}/>
				</Switch>
			</div>
			</div>
	  </Router>  
	  </>

  )
}

 
